/* OUTERMOST PARENT */
.containerr {
  position: relative;
  width: 100%;
  background: #131313;
  height: 100vh;
  font-weight: 300;
  font-family: 'Lato';
}

/* OUTERMOST PARENT ENDS */

/* NavBar */

.topnav {
  width: auto;
  position: absolute;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.topnav-item {
  position: relative;
  padding: 35px;
  margin: auto;
  text-align: center;
}

.topnav-link {
  height: 100%;
  color: white;
  font-weight: 300;
}

.topnav-item:before {
  content: "";
  position: absolute;
  width: 15%;
  transform-origin: center;
  height: 1px;
  bottom: 30%;
  left: 42%;
  transform: translate(-50%, -50%);
  background-color: #FFF;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}

.topnav-item:hover:before {
  visibility: visible;
  transform: scaleX(4);
}

/* Navbar Ends */

/* Header */

.header {
  position: relative;
  width: auto;
  top: 20%;
}

.header-txt {
  color: white;
  font-weight: 300;
  font-family: 'Lato';
}

/* Header Ends */

/* Content View */

.content-view {
  width: 100%;
  height: 50%;
  position: absolute;
  overflow: auto;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 50%, 50%;
}

.home {
  color: white;
}

.home p {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 75%;
  font-size: 200%;
  font-weight: 100;
  animation: p-animation 0.5s 1;
}

@keyframes p-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Apps */


/* Appcard */
.app-card {
  opacity: 95%;
  position: absolute;
  padding-top: 10px;
  top: 50%;
  left: 50%;
  width: 35%;
  height: 75%;
  border: 0px solid black;
  border-radius: 5px;
  box-shadow: 10px 10px 2px rgba(0, 0, 0, 0.3);
  background-image: linear-gradient(to right, #1a1a1a, #1f1f1f);
  animation: p-animation 0.5s 1;
  transform: translate(-50%, -50%);
}

/* .app-card-body {
  border: 1px solid yellow;
} */

.app-card-title {
  color: whitesmoke;
  font-weight: 100;
  position: relative;
  text-align: center;
}

.app-card-text {
  color: aliceblue;
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  justify-content: center;
}

.app-card-btn {
  color: aliceblue;
  border: 1px solid aliceblue;
  padding: 4% 2%;
  width: 40%;
  height: auto;
  text-align: center;
  text-transform: capitalize;
  position: absolute;
  bottom: 0;
  left: 50%;
  overflow: hidden;
  text-decoration: none;
  transform: translate(-50%, -50%);
  transition: all 0.4s ease-in-out;
}

.app-card-btn::before {
  content: '';
  z-index: -1;
  position: absolute;
  left: 0;
  width: 200%;
  height: 200%;
  top: 200%;
  transition: all 0.4s ease-in-out;
  background-color: aliceblue;
}

.app-card-btn:hover::before {
  animation: btn-animation 0.4s 1;
  top: 0%;
}

.app-card-btn:hover {
  color: black;
  text-decoration: none;
}

@keyframes btn-animation {
  0% {
    top: 200%;
  }
  100% {
    top: 0%;
  }
}
/* Appcard Ends */
/* Apps Ends */

/* Contact */
.contact {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
/* Contact Ends */

.scrollable {
  padding: 4px;
  width: 100%;
  overflow: hidden;
}

/* Content View Ends */
/* Footer */

.footer {
  width: 100%;
  position: absolute;
  bottom: 0%;
  margin-bottom: 1rem;
}

.button-white-footer {
  color: white;
}

/* Footer Ends */